.hamburger {
    display: none; // Hidden by default
    flex-direction: column;
    gap: 5px;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 1100;

    @include mq("bootstrap-lg", "max") {
        display: flex;
    }

    span {
        display: block;
        width: 30px;
        height: 3px;
        background: #333;
        transition: transform 0.3s, opacity 0.3s;
    }

    &.is-open {
        span:nth-child(1) {
            transform: translateY(8px) rotate(45deg);
        }

        span:nth-child(2) {
            opacity: 0;
        }

        span:nth-child(3) {
            transform: translateY(-8px) rotate(-45deg);
        }
    }
}
