.pagetitlebar {
  position: relative;

  &__picture{
    img{
      display: block;
      @include mq("tablet","max"){
        aspect-ratio: 375 / 179;
        object-fit: cover;
      }
    }
  }

  &__text {
    color: $color-flame;
    font-family: $font-secondery;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 100%;
    font-size: 4.107rem;
    line-height: 1;

    @include mq("bootstrap-lg", "max") {
      font-size: 3.6rem;
    }
    @include mq("tablet","max"){
      font-size: 3.1rem;
    }

    span {
      display: inline-block;
      margin-right: 10px;
      font-size: 4.596rem;
      vertical-align: text-bottom;

      @include mq("bootstrap-lg", "max") {
        font-size: 4rem;
        margin-right: 5px;
      }
      @include mq("tablet","max"){
        display: block;
        font-size: 2.8rem;
      }
    }
  }
}