.scroll-up {
  display: none;
  position: fixed;
  bottom: 50px;
  right: 24px;
  cursor: pointer;
  transition: all .35s ease;
  z-index: 2;
  @include mq("tablet", "max") {
    bottom: 10px;
    right: 15px;
  }

  img {
    width: 62px;

    @include mq("tablet", "max") {
      width: 35px;
    }
  }

  &:hover {
    opacity: 0.8;
  }
}