.category {
  position: relative;
  padding-block: 40px 85px;

  .btn-submit.-shop {
    position: absolute;
    top: -99px;
    right: 13px;

    @include mq("tablet", "max") {
      max-width: 350px;
      padding: 5px 20px;
      top: -73px;

      svg,
      image {
        width: 18px;
      }
    }
  }

  &__wrapper {
    display: flex;
    align-items: flex-start;
    gap: 19px;

    @include mq("tablet", "max") {
      flex-direction: column;
      gap: 50px;
    }
  }

  &__main {
    flex: 1;

    .category__products {
      // margin-right: 3px;

      // @include mq("tablet", "max") {
      //   margin-right: 0;
      // }
    }

    &-title {
      font-weight: 600;
      font-size: 1.8rem;
    }
  }

  &__intro {
    margin-block: 50px 40px;
    // margin-left: 3px;

    &-heading {
      width: fit-content;
      font-family: $font-primary;
      background: #009944;
      color: #ffffff;
      padding: 3px 6.5px 4px;
      font-size: 2.021rem;
      font-weight: 600;
    }

    &-text {
      background-color: $color-cultured;
      line-height: calc(20 / 14);
      padding: 16px 29px 18px 19px;
    }
  }

  &__products {

    &-more {
      transition: all .3s ease;
      padding-top: 76px;
    }
  }

  .ofukuwake {
    &__button {
      padding-top: 76px;
    }

    &__row {
      gap: 35px 46px;
      transition: opacity 0.3s ease, max-height 0.3s ease;

      @include mq("bootstrap-lg", "max") {
        gap: 20px;
      }

      // @include mq("tablet","max"){

      // }
      .hidden-items {
        opacity: 0;
        visibility: hidden;
        max-height: 0;
        overflow: hidden;
        transition: opacity 0.3s ease, max-height 0.3s ease, visibility 0s 0.3s;
        position: absolute;
      }

      .hidden-items.show {
        position: relative;
        opacity: 1;
        visibility: visible;
        max-height: auto;
        transition: opacity 0.3s ease, max-height 0.3s ease;
      }

    }

    &__col {
      width: calc((100% / 2) - (46px / 2));

      @include mq("bootstrap-lg", "max") {
        width: calc((100% / 2) - (20px / 2));
      }
      @include mq("tablet", "max") {
        width: calc((100% / 2) - (20px / 2));
      }

      @include mq("phablet", "max") {
        width: 100%;
      }

      img {
        display: block;
        aspect-ratio: 376 / 203;
        object-fit: cover;
      }
    }
  }

  &__sidebar {
    position: sticky;
    top: 10px;
    flex: 0 1 250px;
    margin-top: 22px;
    
    @include mq("bootstrap-lg", "max") {
      flex: 0 1 210px;
    }
    
    @include mq("tablet", "max") {
      margin-top: 0;
      flex: 0 1 100%;
      position: static;
      width: 100%;
    }

    &-list {
      background-color: $color-cultured;
      padding: 29px 31px 32px;
      margin-bottom: 18px;

      @include mq("tablet", "max") {
        // display: flex;
        // justify-content: space-between;
        padding: 10px 15px;
      }
    }

    &-item {
      transition: all .3s ease;
      text-decoration: none;
      color: $color-black;
      display: block;
      font-weight: 600;
      font-size: 1.6rem;
      line-height: calc(24 / 16);

      &:hover {
        opacity: .7;

        @include mq("tablet", "max") {
          opacity: 1;
        }
      }
    }

    .buttons {
      display: flex;
      gap: 16px;
      margin-right: 0;

      @include mq("bootstrap-lg", "max") {
        gap: 10px;
      }

      @include mq("tablet", "max") {
        gap: 20px
      }

      &__link {
        width: 100%;
        min-height: 106px;
        gap: 5px;
        padding: 0 0px 20px;

        @include mq("tablet", "max") {
          min-height: 70px;
          padding: 0 0px 10px;

          img {
            width: 28px;
            height: 20px;
          }
        }

        &:first-child {
          @include mq("tablet", "max") {

            img {
              width: 26px;
              height: 30px;
            }
          }
        }

        &-text {
          font-weight: 600;
          font-size: 1.434rem;

          @include mq("bootstrap-lg", "max") {
            font-size: 1.22rem;
          }
        }
      }
    }

    &-share {
      display: flex;
      align-items: baseline;
      justify-content: flex-end;
      gap: 20px;
      margin-bottom: 20px;

      @include mq("bootstrap-lg", "max") {
        gap: 10px;
      }

      .social {
        gap: 10px;
        @include mq("bootstrap-lg", "max") {
          width: auto;
        }

        img {
          max-width: 18px;
        }

        svg {
          height: 25px;
          width: 25px;
        }

        .line {
          padding: 3px;
          margin-left: 0;
          border-radius: 3px;

          img {
            max-width: 14px;
          }
        }
      }
    }
  }

  &__comment {
    position: relative;
    max-width: 920px;
    margin-inline: auto;
    padding-top: 145px;
    margin-top: 67px;

    &-img {
      position: absolute;
      top: 0;
      z-index: -1;
    }

    &-text {
      background-color: $color-gray3;
      border: 1px solid $color-gray4;
      border-radius: 5px;
      max-height: 260px;
      overflow-y: auto;
      padding: 15px;
      line-height: 1.5;
    }
  }
}