.contact {
  max-width: 670px;
  margin-inline: auto;
  padding: 112px 15px;

  @include mq("tablet", "max") {
    padding: 60px 15px;
  }

  .form {
    &-group {
      margin-bottom: 32px;

      @include mq("tablet", "max") {
        margin-bottom: 20px;
      }

      :last-of-type {
        margin-bottom: 0;
      }
    }

    &-label {
      font-weight: 600;
      font-size: 1.6rem;
      margin-bottom: 12px;
    }

    &-input input,
    &-input textarea {
      cursor: text;
      width: 100%;
      border: 1px solid #E6E6E6;
      font-family: $font-primary;
      border-radius: 4px;
      font-size: 1.6rem;
      padding: 11px;
    }

    &-input textarea {
      min-height: 260px;
      resize: none;
    }

    &-input input:focus,
    &-input textarea:focus {
      border: 1px solid #ccc;
      outline: none;
    }
  }

  .btn {
    &-grp {
      margin-top: 100px;

      @include mq("tablet", "max") {
        margin-top: 50px;
      }
    }
  }
}
