.hero {
    position: relative;
    padding: 135px 0 198px;

    @include mq("bootstrap-lg", "max") {
        padding: 100px 0;
    }

    @include mq("tablet", "max") {
        padding: 200px 15px;
    }

    &__img {
        position: absolute;
        inset: 0;
        z-index: -1;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &__title {
        font-family: $font-secondery;
        font-size: 9.4rem;
        font-weight: 400;
        line-height: 1.3;
        text-align: center;
        color: $color-flame;
        margin-bottom: 58px;

        @include mq("bootstrap-lg", "max") {
            font-size: 7rem;
            margin-bottom: 30px;
        }

        @include mq("tablet", "max") {
            font-size: 3.3rem;
            margin-bottom: 20px;
        }

        span {
            font-size: 8.4rem;

            @include mq("bootstrap-lg", "max") {
                font-size: 6.4rem;
            }

            @include mq("tablet", "max") {
                font-size: 3rem;
            }
        }
    }
}