.water-wave {
    margin-top: -72px;
}

.sharing {
    background-color: $color-primary;
    position: relative;
    padding: 50px 0;

    &-upwave,
    &-downwave {
        .onde {
            position: relative;
            width: 100%;
            margin-bottom: -7px;
            /*Fix for safari gap*/
            z-index: -1;
            height: 25px;

            @include mq("tablet") {
                height: 50px;
            }

            @include mq("desktop") {
                height: 100px;

            }
        }

        .parallaxonde>use {
            animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
        }

        .parallaxonde>use:nth-child(1) {
            animation-delay: -2s;
            animation-duration: 8s;
        }
    }

    &-downwave {
        transform: scaleY(-1);

        .parallaxonde>use:nth-child(1) {
            animation-delay: -5s;
            animation-duration: 8s;
        }
    }


    &__text {
        line-height: calc(24/14);
        color: $color-white;
        font-weight: 600;
        font-size: 1.3rem;

        @include mq("tablet") {
            font-size: 1.4rem;
        }
    }

    &__row {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-top: 25px;
        gap: 100px;

        @include mq("tablet") {
            gap: 35px;
            margin-top: 64px;
        }
    }

    &__circle {
        max-width: 110px;
        width: 100%;
        margin: 0 auto;
        height: 110px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100px;
        border: 1px solid $color-white;
        line-height: 1.2;
        font-size: 1.8rem;
        color: $color-white;
        text-align: center;
        font-weight: 600;
        position: relative;

    }

    &__col {
        width: 100%;
        position: relative;

        @include mq("tablet") {
            width: calc((100% / 3) - (35px * 2) / 3);
        }

        &:not(:last-of-type) {
            &::after {
                content: "+";
                display: block;
                font-size: 1.8rem;
                line-height: 1.2;
                font-weight: 600;
                color: $color-white;
                position: absolute;
                bottom: -60px;
                left: 50%;
                transform: translateX(-50%);

                @include mq("tablet") {
                    content: none;
                }
            }

            @include mq("tablet") {
                .sharing__circle {
                    &::after {
                        content: "+";
                        display: block;
                        font-size: 1.8rem;
                        line-height: 1.2;
                        font-weight: 600;
                        color: $color-white;
                        position: absolute;
                        bottom: -30px;
                        left: 50%;
                        transform: translateX(-50%);

                        @include mq("tablet") {
                            bottom: unset;
                            right: -92px;
                            top: 50%;
                            left: unset;
                            transform: translateY(-50%);

                        }
                    }
                }
            }

        }

        .sharing__text {
            margin-top: 33px;

            @include mq("tablet") {
                min-height: 151px;
            }
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 27px;
        text-align: center;
        margin-top: 50px;
        @include mq("tablet","max"){
            flex-direction: column;
        }

        a {
            text-decoration: none;
            color: $color-primary;
            background-color: $color-white;
            border: 1px solid transparent;
            max-width: 273px;
            height: 51px;
            width: 100%;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            border-radius: 25.5px;
            font-size: 1.6rem;
            line-height: 1.2;
            font-weight: 600;
            transition: all 0.25s ease;


            &:hover {
                background-color: $color-primary;
                color: $color-white;
                border: 1px solid $color-white;
            }
        }

        &.no-hover {
            a {
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}
