.btn-submit {
  cursor: pointer;
  width: 100%;
  border-radius: 30px;
  background-color: $color-primary;
  color: $color-white;
  border: none;
  display: flex;
  align-items: center;
  margin-inline: auto;
  transition: all .3s ease;
  gap: 15px;
  padding: 18px 80px;
  max-width: 420px;

  @include mq("tablet", "max") {
    padding: 18px 60px 18px 40px;
    justify-content: center;
    gap: 10px;
  }

  &:hover {
    opacity: 0.7;
    @include mq("tablet", "max") {
      opacity: 1;
    }
  }

  &.-shop {
    text-decoration: none;
    padding: 15.4px 58px;
    @include mq("tablet", "max") {
      padding: 15px 20px;
      justify-content: center;
    }

    span {
      &::after {
        right: -28px;
      }
      @include mq("tablet", "max") {
        display: flex;
        align-items: center;
        gap: 10px;
        &::after{
          position: relative;
          display: block;
          top: 0;
          left: 0;
          transform: none;
        }
      }
    }
  }

  span {
    position: relative;
    font-weight: 600;
    font-size: 1.8rem;
    line-height: 1.3334;

    @include mq("tablet", "max") {
      font-size: 1.6rem;
    }

    &::after {
      background: url(../img/common/icon-play.svg) no-repeat;
      background-position: center;
      background-size: cover;
      position: absolute;
      content: '';
      width: 21px;
      height: 21px;
      right: -45px;
      top: 50%;
      transform: translateY(-50%);

      @include mq("tablet", "max") {
        width: 18px;
        height: 18px;
        right: -25px;
      }

    }
  }
}

// primary-btn 
.primary__btn {
  max-width: 420px;
  width: 100%;
  border-radius: 30px;
  padding: 20px 0;
  background-color: $color-white;
  border: 1px solid $color-primary;
  font-size: 1.8rem;
  font-weight: 600;
  color: $color-primary;
  font-family: $font-primary;
  text-align: center;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.25s ease;

  @include mq("tablet", "max") {
    max-width: 320px;
    padding: 15px 0;
    font-size: 1.6rem;

    &:hover {
      background-color: $color-white !important;
      color: $color-primary !important;
    }
  }

  &:hover {
    background-color: $color-primary;
    color: $color-white;
  }
}