.footer {
    background-color: $color-gray;

    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 11px 17px 10px 30px;

        @include mq("tablet", "max") {
            flex-direction: column;
            gap: 10px;
            padding: 15px 30px;
        }
    }

    &__logo {
        display: flex;
        align-items: center;
        gap: 22px;

        &-copyright {
            font-size: 1.4rem;
        }
    }

    &__end {
        display: flex;
        align-items: center;
        gap: 32px;

        @include mq("tablet", "max") {
            flex-direction: column;
            gap: 15px;
        }
    }

    &__icons {
        display: flex;
        gap: 20px;

        img {
            display: block;
        }
    }

    &__category {
        display: flex;
        align-items: center;
        gap: 28px;

        &-link {
            position: relative;
            text-decoration: none;
            color: $color-black;
            font-size: 1.2rem;

            &:first-child {
                &::after {
                    position: absolute;
                    content: '|';
                    font-size: 1.2rem;
                    right: -16px;
                }
            }
        }
    }

    a {
        transition: all .3s ease;

        &:hover {
            opacity: .7;

            @include mq("tablet", "max") {
                opacity: 1;
            }
        }
    }

    .line {
        background-color: #00b900;
        text-decoration: unset;
        padding: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        border-radius: 5px;

        .line-icon {
            img {
                display: flex;
            }
        }

        .line-text {
            font-size: 0.9rem;
            color: $color-white;
            margin-top: 1px;

            @include mq("bootstrap-lg", "max") {
                font-size: 1.2rem;
            }
        }
    }
}