html {
    font-size: 10px;
    //for footer position bottom

    // height: 100%;
}

body {
    font-family: $font-primary;
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-black;
    line-height: 1.2;
    background: $color-white;
    letter-spacing: 0.05em;

    //for footer position bottom

    // display: flex;
    // flex-direction: column;
    // min-height: 100%;
}

.container {
    display: block;
    max-width: 960px;
    padding: 0 15px;
    margin: 0 auto;

    @include mq("tablet", "max") {
        max-width: 100%;
    }

    &.-sm {
        max-width: 830px;

        @include mq("tablet", "max") {
            max-width: 100%;
        }
    }

    &.-md {
        max-width: 880px;

        @include mq("tablet", "max") {
            max-width: 100%;
        }
    }

    &.-lg {
        max-width: 1100px;

        @include mq("tablet", "max") {
            max-width: 100%;
        }
    }
}

%reset-ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

* {
    box-sizing: border-box;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

.kagoshima-p {
    font-size: 14px;
}

.error,
.success {
    text-align: center;
    font-size: 14px;
    padding: 10px;
    margin-top: 20px;
}

.error {
    color: #ff0000;
    border: 1px solid #ff0000;
}

.success {
    color: #00ff00;
    border: 1px solid #00ff00;
}