.company {
  background-color: $color-gray;
  padding: 39px 0 156px;

  @include mq("tablet", "max") {
    padding: 75px 0 79px;
  }

  &__wrapper {
    max-width: 900px;
    padding-inline: 15px;
    margin-inline: auto;
  }

  &__header {
    margin-bottom: 60px;
    text-align: center;

    &-jp {
      font-weight: 600;
      font-size: 1.8rem;
      margin-top: 10px;
    }
  }

  &__table {
    &-tr {
      display: flex;
      align-items: center;

      &:not(:last-of-type) {
        padding-bottom: 12px;
        border-bottom: 1px solid $color-gray2;
      }

      &:not(:first-of-type) {
        margin-top: 20px;
      }
    }

    &-th {
      text-align: start;
      padding-left: 4px;
      width: 181px;

      @include mq("tablet", "max") {
        width: 90px;
      }
    }

    &-td {
      text-align: start;
      width: calc(100% - 181px);

      @include mq("tablet", "max") {
        width: calc(100% - 90px);
      }
    }

    &-txt {
      font-size: 1.4rem;
      line-height: 1.2;
      font-weight: 600;

      @include mq("tablet", "max") {
        font-size: 1.3rem;
      }
    }

  }

  &__iframe {
    line-height: 0;
    margin-top: 23px;
    padding-inline: 3.5px;

    iframe {
      width: 100%;
      height: 298px;

      @include mq("tablet", "max") {
        height: 320px;
      }
    }
  }
}