// category-detail
.category-detail {
    &__block{
        @include mq("tablet", "max") {
            margin-top: 50px;
        }
    }
    .heading__sec {
        text-align: left;
        padding: 28px 0 40px;

        @include mq("tablet", "max") {
            padding: 20px 0 30px;
        }
    }

    .heading__primary {
        padding: 0 0 65px;

        @include mq("tablet", "max") {
            padding: 0 0 40px;
        }

        &::before {
            display: none;
        }
    }

    .kagoshima__text {
        &.pt {
            padding-top: 40px;
        }
    }

    .ofukuwake {
        &__row {
            gap: 49px 9px;
        }

        &__col {
            width: calc((100% / 3) - (9px * 2) / 3);
            @include mq("tablet", "max") {
                width: calc((100% / 2) - (9px / 2));
            }

            @include mq("phablet", "max") {
                width: 100%;
            }

            img {
                // display: block;
                width: 100%;
                aspect-ratio: 301 / 203;
                object-fit: cover;
            }
        }
    }

    .category {

        &__top,
        &__bottom {
            .kagoshima__text {
                line-height: calc(20 / 14);
            }
        }

        &__wrapper {
            gap: 40px;
            padding-inline: 5px;
        }

        &__main {
            // flex: 0 1 767px;
            margin-bottom: 175px;

            @include mq("tablet", "max") {
                // margin-bottom: 80px;
                margin-bottom: 20px;
            }
        }

        &__intro {
            margin-block: 0;
            margin-bottom: 20px;

            &-flex {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-top: 8px;

                @include mq("tablet", "max") {
                    margin-top: 20px;
                }
            }

            &-heading {
                font-size: 1.4rem;
                padding: 4px 7px;
            }

            &-date {
                font-weight: 600;
                font-size: 1rem;
            }
        }

        &__bottom {
            padding-top: 50px;

            @include mq("tablet", "max") {
                padding-top: 40px;
            }
        }

        &__subheading {
            font-size: 2.2rem;
            font-family: $font-primary;
            font-weight: 600;
            color: $color-black;
            text-align: left;
            padding-bottom: 25px;
        }
    }

    &__content {
        max-width: 965px;
        margin: 0 auto;
        padding-inline: 15px;
    }
}