.section {
    &-header {
        margin-bottom: 42px;
        text-align: center;

        &__top {
            font-size: 3.0rem;
            line-height: 1.2;
            font-weight: 600;
        }

        &__btm {
            font-size: 1.8rem;
            line-height: 1.2;
            font-weight: 600;
            margin-top: 24px;
        }

        &.-white {

            .section-header__top,
            .section-header__btm {
                color: $color-white
            }
        }
    }
}

// kagoshima
.kagoshima {
    &__text {
        font-size: 1.4rem;
        font-family: $font-primary;
        font-weight: 600;
        letter-spacing: 0.05em;
        color: $color-black;
        text-align: left;

        // @include mq("bootstrap-lg", "max") {
        //     font-size: 1.2rem;
        // }

        &.center{
            text-align: center;
        }

        &.pb-15 {
            padding-bottom: 15px;
        }

        &.pb-26 {
            padding-bottom: 26px;
        }

        &.padding {
            padding: 15px 0 12px;

            @include mq("tablet", "max") {
                padding: 10px 0;
            }
        }

        &.pr-50 {
            padding-right: 60px;
        }
    }
}

.ofukuwake__content .kagoshima__text{
    @include line-clamp(2) 
}
