.header {
    padding: 0 12px 0 18px;
    height: 70px;
    position: relative;

    @include mq("desktop", "max") {
        padding: 0 10px;
    }

    @include mq("bootstrap-lg", "max") {
        height: 65px;
    }

    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 50px;

        @include mq("desktop", "max") {
            gap: 10px;
            justify-content: space-around;
        }

        @include mq("bootstrap-lg", "max") {
            gap: 10px;
            justify-content: space-between;
        }
    }

    &__box {
        display: flex;
        justify-content: center;
        align-items: center;

        @include mq("bootstrap-lg", "max") {
            position: fixed;
            top: 0;
            right: -100%;
            // width: 100%;
            width: 375px;
            height: 100vh;
            background: #fff;
            box-shadow: -4px 0 6px rgba(0, 0, 0, 0.1);
            transition: right 0.3s ease-in-out;
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            flex-direction: column;
            padding: 90px 20px;
            z-index: 1;
            gap: 100px;

            &.is-open {
                right: 0;
            }
        }

        @include mq("phablet", "max") {
            width: 100%;
        }
    }
}


.logo {
    align-self: flex-end;
    transition: all 0.3s ease;

    &:hover {
        opacity: 0.7;

        @include mq("tablet", "max") {
            opacity: 1;
        }
    }

    img {
        display: block;

        @include mq("desktop", "max") {
            max-width: 155px;
        }

        @include mq("bootstrap-lg", "max") {
            max-width: 100%;
        }
    }
}

.nav {
    margin-right: 45px;

    @include mq("desktop", "max") {
        margin-right: 10px;
    }

    &__list {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;

        @include mq("desktop", "max") {
            gap: 10px;
        }

        @include mq("bootstrap-lg", "max") {
            flex-direction: column;
            align-items: flex-start;
            gap: 50px;

        }
    }

    &__item {

        a {
            font-size: 1.4rem;
            font-weight: 600;
            color: $color-black;
            text-decoration: unset;
            transition: all 0.3s ease;

            @include mq("desktop", "max") {
                font-size: 1.2rem;
            }

            @include mq("bootstrap-lg", "max") {
                font-size: 1.4rem;
            }

            &:hover {
                opacity: 0.7;
                color: $color-primary;

                @include mq("tablet", "max") {
                    opacity: 1;
                    color: $color-black;
                }
            }
        }
    }
}

.buttons {
    display: flex;
    justify-content: center;
    gap: 1px;
    align-items: stretch;
    margin-right: 25px;

    @include mq("desktop", "max") {
        margin-right: 10px;
    }

    @include mq("bootstrap-lg", "max") {
        width: 100%;
        gap: 20px;

    }

    &__link {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        gap: 3px;
        text-decoration: unset;
        min-height: 70px;
        min-width: 81px;
        background-color: $color-cultured;
        color: $color-black;
        padding: 0 0px 13px;
        transition: all 0.3s ease;

        @include mq("bootstrap-lg", "max") {
            width: 50%;
            max-width: 100%;
        }

        p {
            font-size: 1rem;
            font-weight: 600;
        }

        &:hover {
            background-color: $color-primary;
            color: $color-white;

            .norm {
                filter: invert(1);
            }

            @include mq("tablet", "max") {
                background-color: $color-cultured;
                color: $color-black;
            }
        }

        .norm {
            transition: all 0.3s ease;
        }
    }
}

.social {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    position: relative;

    @include mq("desktop", "max") {
        gap: 10px;
    }

    @include mq("bootstrap-lg", "max") {
        width: 100%;
        gap: 40px;
    }

    &__link {
        transition: all 0.3s ease;
        display: flex;

        img {
            @include mq("bootstrap-lg", "max") {
                max-width: 30px;
                width: 100%;
            }
        }

        &.line {
            background-color: #00b900;
            text-decoration: unset;
            padding: 5px;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 5px;
            margin-left: -5px;

            @include mq("desktop", "max") {
                margin-left: 0;
            }

            .line-icon {
                img {
                    display: flex;
                }
            }

            .line-text {
                font-size: 0.9rem;
                color: $color-white;
                margin-top: 1px;

                @include mq("bootstrap-lg", "max") {
                    font-size: 1.2rem;
                }
            }
        }


        &:hover {
            opacity: 0.7;

            @include mq("tablet", "max") {
                opacity: 1;
            }
        }
    }

    .copied {
        position: absolute;
        font-size: 1.2rem;
        color: $color-white;
        word-break: keep-all;
        background-color: #777;
        padding: 3px 5px;
        border-radius: 50px;
        right: 0;
        bottom: -20px;
    }

}

.no-scroll {
    overflow: hidden;
}