//services
.services {
    padding: 80px 0 70px;

    @include mq("tablet", "max") {
        padding: 40px 0;
    }

    &__col {
        padding-top: 35px;
        @include mq("tablet", "max") {
            padding-top: 30px;
        }
    }

    &__img {
        img {
            display: block;
            object-fit: cover;
            aspect-ratio: 805 / 281;
        }
    }

    &__content {
        padding-top: 20px;

        @include mq("tablet", "max") {
            padding-top: 10px;
        }
        .kagoshima__text:first-child{
            margin-bottom: 5px;
        }
    }

    &__link {
        font-family: $font-primary;
        font-size: 1.4rem;
        font-weight: 600;
        color: $color-black;
        text-align: left;
        text-decoration: none;
        transition: all 0.25s ease;

        @include mq("tablet", "max") {
            font-size: 1.2rem;
            word-wrap: break-word;
            overflow-wrap: break-word;
            white-space: normal;
            display: block;

            &:hover {
                opacity: 1 !important;
            }
        }

        &:hover {
            opacity: 0.7;
        }
    }

    &__row {
        display: flex;
        justify-content: center;
        gap: 50px;

        @include mq("bootstrap-lg", "max") {
            gap: 30px;
        }
        @include mq("tablet", "max") {
            gap: 15px;
        }

        @include mq("phablet", "max") {
            flex-direction: column;
        }
    }

    &__tab {
        text-decoration: none;
        width: calc((100% / 2) - (50px * 1) / 2);
        transition: all 0.25s ease;

        @include mq("bootstrap-lg", "max") {
            width: calc((100% / 2) - (30px * 1) / 2);
        }
        @include mq("tablet", "max") {
            width: calc((100% / 2) - (15px * 1) / 2);
        }


        @include mq("tablet", "max") {
            width: 100%;
        }

        &:hover {
            opacity: 0.7;
        }
    }

    &__pic {
        img {
            display: block;
            aspect-ratio: 400 / 206;
            object-fit: cover;
            @include mq("tablet", "max") {
                width: 100%;
            }
        }
    }

    &__block {
        &.margin {
            margin: 53px 0 68px;

            @include mq("tablet", "max") {
                margin: 40px 0;
            }
        }
    }

    &__bg {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $color-cultured;
        height: 194px;
        width: 100%;
    }

    &__bg2 {
        margin: 85px 0 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $color-cultured;
        height: 128px;
        width: 100%;

        @include mq("tablet", "max") {
            margin: 40px 0;
            padding: 0 15px;
        }
    }

    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

//heading
.heading {

    &__top{
        &.pt{
            padding-top: 25px;
        }
    }

    &__sec {
        font-size: 2.4rem;
        font-family: $font-primary;
        font-weight: 600;
        color: $color-black;
        padding-bottom: 43px;
        text-align: center;

        @include mq("tablet", "max") {
            font-size: 2rem;
            padding-bottom: 30px;
        }
    }

    &__primary {
        font-size: 1.8rem;
        font-family: $font-primary;
        font-weight: 600;
        color: $color-black;
        padding: 0 0 25px 60px;
        text-align: left;
        position: relative;

        @include mq("bootstrap-lg", "max") {
            padding: 0 0 25px 50px;
        }

        @include mq("tablet", "max") {
            font-size: 1.6rem;
            padding: 0 0 15px 40px;
        }

        &::before {
            content: "";
            background-image: url("../img/services/arrow.png");
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            width: 47px;
            height: 42px;
            top: 20%;
            left: 0;
            position: absolute;
            transform: translateY(-50%);

            @include mq("bootstrap-lg", "max") {
                width: 30px;
                height: 25px;
            }
        }
    }
}

// ofukuwake
.ofukuwake {
    &__top {
        text-align: center;
        // padding-bottom: 55px;
        padding-bottom: 68px;

        img {
            padding-bottom: 20px;

            @include mq("tablet", "max") {
                width: 200px;
                height: 200px;
                padding-bottom: 15px;
            }
        }
    }

    &__txt {
        font-size: 1.4rem;
        font-weight: 600;
        color: $color-black;
        padding: 15px 0;
    }

    &__row {
        display: flex;
        justify-content: flex-start;
        gap: 49px 9px;
        flex-wrap: wrap;
        transition: opacity 0.3s ease, max-height 0.3s ease;

        @include mq("phablet", "max") {
            justify-content: center;
            flex-direction: column;
        }
  
        .hidden-items {
          opacity: 0;
          visibility: hidden;
          max-height: 0;
          overflow: hidden;
          transition: opacity 0.3s ease, max-height 0.3s ease, visibility 0s 0.3s;
          position: absolute;
        }
  
        .hidden-items.show {
          position: relative;
          opacity: 1;
          visibility: visible;
          max-height: auto;
          transition: opacity 0.3s ease, max-height 0.3s ease;
        }
    }

    &__col {
        width: calc((100% / 3) - (9px * 2) / 3);
        position: relative;

        @include mq("tablet", "max") {
            width: calc((100% / 2) - (9px / 2));
        }

        @include mq("phablet", "max") {
            width: 100%;
        }

        &-title {
            content: "";
            position: absolute;
            top: -14px;
            left: 44px;
            transform: translateX(-50%);
            background: $color-primary;
            color: $color-white;
            padding: 5px 10px;
            font-size: 1.4rem;
            font-family: $font-primary;
            font-weight: 600;
            z-index: 2;
        }

        img {
            display: block;
            width: 100%;
            aspect-ratio: 301 / 203;
            object-fit: cover;
            @include mq("tablet", "max") {
                width: 100%;
            }
        }
        a{
            transition: all .3s ease;
            &:hover{
                opacity: .7;
            }
        }
    }

    &__date {
        font-size: 1rem;
        font-family: $font-primary;
        font-weight: 600;
        color: $color-black;
        padding-bottom: 5px;
        display: block;
    }

    &__content {
    }

    &__btn {
        text-decoration: none;
        font-size: 1rem;
        font-family: $font-primary;
        font-weight: 600;
        color: $color-black;
        display: block;
        text-align: right;
        transition: all 0.25s ease;
        padding-top: 12px;
        padding-right: 10px;

        @include mq("tablet", "max") {
            padding-top: 5px;
        }

        &:hover {
            opacity: 0.7;
        }
    }
    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 100px;

        @include mq("tablet", "max") {
            padding-top: 40px;
        }
    }
}

