.breadcrumb {
  // max-width: 1230px;
  // padding-inline: 15px;
  // margin-top: 125px;
  margin-top: 55px;
  margin-inline: 54px auto;
  overflow: hidden;

  @media (min-width:1201px) {
    max-width: 1090px;
    padding-inline: 15px;
    margin-inline: auto;
  }

  @include mq("desktop", "max") {
    margin-inline: 15px auto;
  }

  @include mq("tablet", "max") {
    margin-top: 30px;
    margin-inline: 15px;
  }

  &__list {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    white-space: nowrap;
    overflow-y: hidden;
  }

  &__link {
    display: block;
    transition: all .3s ease;
    position: relative;
    color: $color-black;
    text-decoration: none;

    &:hover {
      opacity: .7;
    }

    &::after {
      content: '>';
      position: absolute;
      font-size: 1.4rem;
      // top: 50%;
      // transform: translateY(-50%);
      top: -2px;
      right: -15px;
    }
  }

  &__item {
    font-weight: 600;
    font-size: 1.4rem;
  }
}